.singlePost{
    flex: 9;

}

.singlePostWrapper{
    padding: 20px;
    padding-right: 0;

}

.singlePostImg{
    width: 100%;
    height: 300px;
    border-radius: 5px;
}


.singlePostTitle{
    text-align: center;
    margin: 10px;
    font-family: 'Lora', serif;
    font-size: 28px;
    font-weight: 700;
}

.singlePostEdit{
    float: right;
    font-size: 16px;

}

.singlePostIcon{
    margin-left: 10px;
    cursor: pointer;
}

.singlePostIcon:first-child{
    color: teal;
}

.singlePostIcon:last-child{
    color:tomato;
}

.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: #b39656;
    font-family: 'Quicksand', sans-serif;

}

.singlePostDescription{
    color: #666;
    font-size: 18px;

}

.singlePostDescription::first-letter{
    margin-left: 20px;
    min-width: 30px;
    font-size: 30px;
    font-weight: 600px;
}